import Joi from '@hapi/joi';

import { ArchiveSource, ArchiveSourceSchema } from '../archiveSource';
import { Platform } from '../constants';

export interface AndroidKeystore {
  dataBase64: string;
  keystorePassword: string;
  keyAlias: string;
  keyPassword: string;
}

const AndroidKeystoreSchema = Joi.object({
  dataBase64: Joi.string().required(),
  keystorePassword: Joi.string().required(),
  keyAlias: Joi.string().required(),
  keyPassword: Joi.string().required(),
});

interface BaseAndroidJob {
  projectArchive: ArchiveSource;
  platform: Platform.Android;
  projectRootDirectory: string;
  releaseChannel?: string;
  secrets: {
    buildCredentials?: {
      keystore: AndroidKeystore;
    };
    secretEnvs?: Record<string, string>;
  };
}

const BaseAndroidJobSchema = Joi.object({
  projectArchive: ArchiveSourceSchema.required(),
  platform: Joi.string().valid(Platform.Android).required(),
  projectRootDirectory: Joi.string().required(),
  releaseChannel: Joi.string(),
  secrets: Joi.object({
    buildCredentials: Joi.object({ keystore: AndroidKeystoreSchema.required() }),
    secretEnvs: Joi.object().pattern(Joi.string(), Joi.string()),
  }).required(),
});

export { BaseAndroidJob, BaseAndroidJobSchema };
